/*import add from '../img/add.svg'; */
import React, {useState} from 'react';
import { Button, Modal } from "react-bootstrap";
import LazyLoad from 'react-lazyload';
import plus from '../img/plus.svg';
import add from '../img/add.svg';
import deleteIcon from '../img/Delete.svg';
import cancel from "./../logo/cancel.svg";
import './Product.css';
import Heart from '../img/Heart.svg'
import { Link } from 'react-router-dom';


const Product =({name, src, price, weight, description})=>{
	 const [productModal, handleProductModal] = useState(false);
	 const showModal = ()=>{handleProductModal(true)};
	 const hideModal =()=>{handleProductModal(false)}
     return(
     		 <> 
			    <Modal show={productModal} onHide={hideModal} className="product__modal">
					<Modal.Header className="border-0">
					<img
							src={cancel}
							className="close"
							alt="cancel"
							onClick={hideModal}

                   />
					<Modal.Title>
						<Link to="/" className="link">
							<div className="d-flex align-items-center">
								<span className="favourite__icon"><img src={Heart} alt="favourite__icon" /></span>
								<span className="add_to_favourite ms-2">Add to favorite</span>
							</div>
						</Link>
					</Modal.Title>
					</Modal.Header>
					<Modal.Body>
					   <img src={src} alt={name} className="d-block w-50 mx-auto" />
					   <hr className="modal__horizontal__line"/>
					   <h3 className="modal__product__name">{name}</h3>
					   <h4 className="modal__product__price">₦{price}</h4>
					   <p className="modal__product__description">
					     {description}
					   </p>
					</Modal.Body>
					<Modal.Footer className="border-0 ">
						<div className="product__footer d-flex justify-content-between"> 
						 <div className="modal__add__product__wrapper d-flex  align-items-center">
		                     <div className="delete_product p-3">
							 < img src={deleteIcon} />
							 </div>
							 <div className="count_product bg-primary text-white p-3">
								1
							 </div>
							 <div className="add_product  p-3">
								 <img src={add} />
							 </div>
						 </div>
						 <div className="modal__basket__price__wrapper d-flex justify-content-center align-items-center">
		                    <div className="modal__goto__basket p-3">
								  Go to basket
								      
							</div>
							<div className="modal_basket__price p-3">
							   ₦{price}
                            </div>
						 </div>

						</div>
					</Modal.Footer>
				</Modal>
	
			    <LazyLoad>
     		    <div className="favourite"> 
				  <div className="fav-wrapper ">
					  <img src={src} alt={name} className="d-block mx-auto" />
					  <p className="price">₦{price}</p>
					  <p className="name">{name}</p>
					  <p className="weigth">{weight}</p>
				  </div>
				  <span className="add-to-cart" id="add-to-cat" onClick={showModal}><img src={plus} className="add" /></span>
                </div>
				</LazyLoad>
     		 </>
     	)
}

export default Product