import React, { useState } from "react";
import AnimateHeight from "react-animate-height";

const CategoryMenu = ({ showSubMenus = true, activeFunc, value, active }) => {
	const [hideSub, setHideSub] = useState(true);
	const [heightData, setHeightData] = useState(0);

	const toggleSub = () => {
		setHideSub((prev) => !prev);
		setHeightData((prev) => {
			return prev === 0 ? "auto" : 0;
		});
	};

	return (
		<div className="">
			<div
				onClick={() => {
					activeFunc(value);
				}}
				className={`${
					active === value ? "tw-bg-blue-200" : ""
				} tw-flex tw-px-3 tw-items-center tw-py-2`}
			>
				<div className="tw-flex tw-items-center">
					<div style={{ width: "51px", height: "51px" }}>
						<img
							src={require("../../../../assets/images/new-products-icon.svg").default}
							className="tw-w-full"
							alt=""
						/>
					</div>
					<span className="tw-ml-2 tw-font-bold">New Products</span>
				</div>
				<img
					onClick={toggleSub}
					src={require("../../../../assets/icons/chevron-up-blue.svg").default}
					className={`${
						hideSub ? "tw-rotate-180" : "tw-rotate-0"
					} tw-w-3 tw-ml-auto tw-cursor-pointer tw-transform tw-transition-transform`}
					alt=""
				/>
			</div>
			<AnimateHeight
				id="example-panel"
				duration={300}
				height={heightData} // see props documentation below
			>
				<ul
				// style={{ transitionProperty: "height" }}
				// className={`${
				// 	hideSub ? "jt-animate-height-initial" : ""
				// } tw-duration-300 tw-overflow-hidden jt-animate-height-end`}
				>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
					<li className="tw-py-2 tw-ml-10">Beverages</li>
				</ul>
			</AnimateHeight>
		</div>
	);
};

export default CategoryMenu;
